.button {
  border-radius: 5px;
}

.primary-button {
  background-color: #0000a3;
  color: white;
  font-weight: 600;
  font-size: .8rem;
}

.primary-button:hover {
  background-color: #00008f;
}

.secondary-button {
  background-color: #2a2a2f24;
  color: #3a3a3a;
  font-weight: 500;
  font-size: .8rem;
}
.secondary-button:hover {
  background-color: #2a2a2f30;
}

.tag-button{
  background-color: #ffffff;
  border: 1px solid #3a3a3a60;
  font-size: .8rem;
  color: #3a3a3a;
}

.tag-button-active{
  background-color: #1D4ED8;
  color: #ffff;
  font-size: .8rem;
  border: 1px solid transparent;
  font-weight: 600;
}

.image-button {
  background-color: white;
  color: #3a3a3a;
  font-weight: 600;
  font-size: .8rem;
  border: 1px solid #E5E7EB;
}

.image-button:hover{
  background-color: #e5e7eb43;
}

.icon-button{
  // background: transparent;
}

.inverted-button{
  font-size: .8rem;
}

.other-button{
  background-color: white;
  color: #3a3a3a;
  font-weight: 600;
  font-size: .8rem;
  border: 1px solid #E5E7EB;
}

.other-button:hover{
  background-color: #e5e7eb2d;
}

@media screen and (max-width: 440px) {
  .primary-button {
    font-size: .7rem;
  }
  
  .secondary-button {
    font-size: .7rem;
  }
  
  .image-button {
    font-size: .7rem;
  }
}